import React, { Component } from 'react';
import './App.css';
import styled from 'styled-components';
import { Tag, TagView } from './types';
import TagControl from './TagControl';
import axios, { AxiosResponse } from 'axios';
import { IconButton } from 'office-ui-fabric-react';

const api = 'AIzaSyCTe0XGLmSkcWQtJ6hhqii00e-AI1Pnq3k';

const clientId = 'UUpuV6h9dIb1Cq4nIcFapvfg';

const pageSize = 25;

interface Props {}

interface State {
  youTubeItems: YouTubeChannelItem[];
  videos: BarringtonVideo[];
  tags: Tag[];
  selectedTagIds: ReadonlyArray<string>;
  totalCnt: number;
  nextToken?: string;
  prevToken?: string;
  earliestDate?: Date;
}

interface BarringtonVideo {
  id: string;
  tagIds: ReadonlyArray<string>;
  description?: string;
}

const MainContainer = styled.section`
  display: grid;
  align-items: center;
  justify-content: center;
  grid-gap: 0.5em;
  grid-template-rows: repeat(3, auto) 1fr;
  grid-template-areas:
    'MainTitle'
    'TagContainer'
    'VideosTitle'
    'VideoContainer';
  width: 100%;
  height: 100%;
`;

const HeaderText = styled.h4`
  color: ${props => props.theme.lightColor};
  margin: 0em 0.5em;
  letter-spacing: 2px;
`;

const MainTitle = styled.h4`
  grid-area: MainTitle;
  color: ${props => props.theme.lightColor};
  letter-spacing: 2px;
  text-transform: uppercase;
`;

const VideosTitle = styled.h4`
  grid-area: VideosTitle;
  display: flex;
  align-items: center;
  color: ${props => props.theme.lightColor};
  margin: 0em 0.5em;
  letter-spacing: 2px;
  text-transform: uppercase;
  justify-self: end;
`;

const TagContainer = styled.section`
  grid-area: TagContainer;
  display: flex;
  align-items: center;
  width: 100%;
  flex-wrap: wrap;
  border: 0px solid;
  padding: 0.5em;
`;

const VideoContainer = styled.section`
  grid-area: VideoContainer;
  display: grid;
  align-self: center;
  align-items: flex-start;
  grid-template-columns: repeat(4, 300px);
  grid-auto-rows: 200px;
  grid-gap: 0.5em;
  border: 0px solid;

  @media (max-width: 720px) {
    grid-template-columns: auto;
  }
`;

const IFrame = styled.iframe`
  border: none;
  justify-self: center;
`;

const StyledIconButton = styled(IconButton)`
  color: white;
`;

const Video = ({ id }: BarringtonVideo) => (
  <IFrame
    key={id}
    title={id}
    src={`https://www.youtube.com/embed/${id}?controls=0`}
  />
);

const renderTag = (onClick: (id: string) => void) => (t: TagView) => (
  <TagControl
    key={t.id}
    onClick={onClick}
    confirmDeleteText="Are you sure you want to remove tag?"
    tag={t}
  />
);

const createTagFromName = (name: string): Tag => ({ id: name, name });

const getDistinctTags = (tags: string[], currVid: BarringtonVideo) =>
  currVid.tagIds && currVid.tagIds.length
    ? Array.from(new Set([...tags, ...currVid.tagIds]))
    : [...tags];

const createTagView = (tagIds: ReadonlyArray<string>) => (t: Tag) => ({
  ...t,
  isSelected: tagIds.includes(t.id),
  allowDelete: false,
  allowSelect: false
});

const getFilteredVideos = (selectedTagIds: ReadonlyArray<string>) => (
  vid: BarringtonVideo
) =>
  vid.tagIds.length === 0
    ? false
    : selectedTagIds.length > 0
    ? vid.tagIds.some(t => selectedTagIds.includes(t))
    : true;

const sortString = (a: TagView, b: TagView) => a.name.localeCompare(b.name);

class App extends Component<{}, State> {
  public state: Readonly<State> = {
    youTubeItems: [],
    videos: [],
    selectedTagIds: [],
    tags: [],
    totalCnt: 0
  };

  public getVideoResultsByPublishDate = async (publishDate: Date) => {
    const baseUrl = 'https://content.googleapis.com/youtube/v3/playlistItems';
    const url = `${baseUrl}?&part=snippet&maxResults=${pageSize}&playlistId=${clientId}&order=date&publishedBefore=${publishDate}&key=${api}`;

    const resp: AxiosResponse<YouTubeChannelData> = await axios.get(url);

    return resp.data.items;
  };

  public getVideoResults = async (pageToken?: string) => {
    const baseUrl = 'https://www.googleapis.com/youtube/v3/playlistItems';
    const url = `${baseUrl}?&part=snippet&mine=true&maxResults=${pageSize}&order=date&publishedBefore=${new Date()}&playlistId=${clientId}&key=${api}`;

    const resp: AxiosResponse<YouTubeChannelData> = await axios.get(
      pageToken ? `${url}&pageToken=${pageToken}` : url
    );
    console.dir(resp.data.pageInfo.totalResults);

    const videos: BarringtonVideo[] = resp.data.items.map(x => ({
      id: x.snippet.resourceId.videoId,
      description: x.snippet.title,
      tagIds: []
    }));

    const tagVideos = await Promise.all(
      videos.map(async (x: BarringtonVideo) => {
        const r = await axios.get(
          `https://www.googleapis.com/youtube/v3/videos?&part=snippet&id=${
            x.id
          }&key=${api}`
        );

        const snip = r.data.items[0].snippet;
        return (await snip.tags) ? { ...x, tagIds: snip.tags } : x;
      })
    );
    // console.dir(tagVideos);
    // const distinctTags = tagVideos.reduce(getDistinctTags);

    this.setState({
      youTubeItems: resp.data.items,
      videos: tagVideos,
      tags: tagVideos.reduce(getDistinctTags, []).map(createTagFromName),
      totalCnt: resp.data.pageInfo.totalResults - 2,
      prevToken: resp.data.prevPageToken,
      nextToken: resp.data.nextPageToken,
      earliestDate: new Date(resp.data.items[0].snippet.publishedAt)
    });

    const nextVideos = await this.getVideoResultsByPublishDate(
      new Date(resp.data.items[0].snippet.publishedAt)
    );

    // this.setState(prevState => ({
    //   youTubeItems: [...prevState.youTubeItems, ...nextVideos]
    // }));
  };

  public componentDidMount = () => {
    this.getVideoResults();
  };

  public handleTagSelection = (id: string) =>
    this.setState(prevState => ({
      selectedTagIds: prevState.selectedTagIds.includes(id)
        ? prevState.selectedTagIds.filter(p => p !== id)
        : [...prevState.selectedTagIds, id]
    }));

  render() {
    const {
      prevToken,
      nextToken,
      selectedTagIds,
      videos,
      tags,
      totalCnt,
      youTubeItems
    } = this.state;
    const filteredVideos = videos.filter(getFilteredVideos(selectedTagIds));

    return (
      <MainContainer className="App">
        <MainTitle>Coach Lewis' Teams Highlights</MainTitle>
        <TagContainer>
          <HeaderText>TAGS</HeaderText>
          {tags
            .map(createTagView(selectedTagIds))
            .sort(sortString)
            .map(renderTag(this.handleTagSelection))}
        </TagContainer>
        <VideosTitle>
          {this.state.earliestDate && this.state.earliestDate.toString()}
          Videos: {filteredVideos.length} of {totalCnt}
          <StyledIconButton
            disabled={prevToken === undefined}
            iconProps={{ iconName: 'ChevronLeftSmall' }}
            onClick={() => this.getVideoResults(prevToken)}
          />
          <StyledIconButton
            disabled={nextToken === undefined}
            iconProps={{ iconName: 'ChevronRightSmall' }}
            onClick={() => this.getVideoResults(nextToken)}
          />
        </VideosTitle>
        <VideoContainer>{filteredVideos.map(Video)}</VideoContainer>
      </MainContainer>
    );
  }
}

export default App;

interface YouTubeChannelData {
  kind: string;
  etag: string;
  nextPageToken?: string;
  prevPageToken?: string;
  pageInfo: PageInfo;
  items: YouTubeChannelItem[];
}

interface YouTubeChannelItem {
  kind: string;
  etag: string;
  id: string;
  snippet: Snippet;
}

interface Snippet {
  publishedAt: string;
  channelId: string;
  title: string;
  description: string;
  thumbnails: Thumbnails;
  channelTitle: string;
  playlistId: string;
  position: number;
  resourceId: ResourceId;
}

interface ResourceId {
  kind: string;
  videoId: string;
}

interface Thumbnails {
  default: Default;
  medium: Default;
  high: Default;
  standard: Default;
  maxres: Default;
}

interface Default {
  url: string;
  width: number;
  height: number;
}

interface PageInfo {
  totalResults: number;
  resultsPerPage: number;
}

interface YouTubeVideo {
  kind: string;
  etag: string;
  pageInfo: PageInfo;
  items: YouTubeChannelItem[];
}

interface YouTubeChannelItem {
  kind: string;
  etag: string;
  id: string;
  snippet: Snippet;
}

interface Snippet {
  publishedAt: string;
  channelId: string;
  title: string;
  description: string;
  thumbnails: Thumbnails;
  channelTitle: string;
  tags: string[];
  categoryId: string;
  liveBroadcastContent: string;
  localized: Localized;
}

interface Localized {
  title: string;
  description: string;
}

interface Thumbnails {
  default: Default;
  medium: Default;
  high: Default;
  standard: Default;
  maxres: Default;
}

interface Default {
  url: string;
  width: number;
  height: number;
}

interface PageInfo {
  totalResults: number;
  resultsPerPage: number;
}
